@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}
.app {
  background: url(../components/images/52452427-seamless-pattern-for-todo-list-universal-background.jpg);
  position: absolute;
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.app-container {
  padding: 20px;
  width: 600px;
  min-height: 500px;
  background: rgba(255, 255, 255, 0.5);
  /* filter: blur(5px); */
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header h1 {
  text-align: center;
  text-transform: uppercase;
}
.header p {
  text-align: right;
}
.nav-panel {
  display: flex;
  margin-top: 25px;
}

.todo-item {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}
.btn{
    width: 70px;
    height: 25px;
    outline: none;
    border: none;
    border-radius: 5%;
}
.btn.done{
background: green;
}
.btn.important{
    background: yellow;
}
.btn.delete:hover{
background: red;
}
.new-todo {
  text-align: center;
  /* margin-top: 350px; */
}
.new-todo input {
  width: 150px;
  height: 25px;
  font-size: 15px;
  padding-left: 5px;
  border-radius: 5%;
  border: none;
  outline: none;
}
.new-todo button {
  height: 25px;
  border: none;
  background: rgb(188, 188, 255);
  border-radius: 10%;
  margin-top: -8px;
  margin-left: 5px;
  width: 70px;
}
.inp-search {
  text-align: center;
  margin-left: 60px;
  border: none;
  outline: none;
  border-radius: 5%;
  height: 25px;
}
.nav-box {
  display: flex;
  column-gap: 5px;
}
.nav-btn{
  width: 50px;
  background: rgb(188, 188, 255);
  border: none;
  border-radius: 5%;
}
.todo-item p.done{
  text-decoration: line-through;
}
.todo-item p.important {
  text-decoration: underline;
  color: yellow;
  font-weight: bold;
}
.nav-btn.active{
  background: aqua;
}
.color-descr{
  color: red;
  font-weight: bold;
}

